/*------ selectbox------------------------------------------------*/
.selectbox {
	vertical-align: middle;
	cursor: pointer;
	width: 100%;
}
.selectbox .select {
	width: 100%;
	height: 30px;
	padding: 0 45px 0 20px;
	margin-top:18px;
}
.selectbox .select .text {
	display: block;
	width: 100%;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	overflow: hidden;
	font-size:13px;
	text-align:left;
}
.selectbox .trigger {
	position: absolute;
	top: 0;
	right: 0;
	width: 34px;
	height: 100%;
 
}
.selectbox .trigger .arrow {
	position: absolute;
	top: 4px;
	right: 12px;
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-top: 5px solid #ff595e;
	width: 0;
	height: 0;
	overflow: hidden;
 
 
}
.selectbox:hover .arrow {
	opacity: 1;
 
}
.selectbox .dropdown {
	top: 53px;
	width: 100%;
	margin: 0;
	padding: 10px 12px;
	background: #FFF;
	border: 1px solid #eee;
	border-radius: 4px;
	text-align:left;
 
 
}
.selectbox li {
	padding: 5px 10px 6px;
	color: #666;
	font-size:13px;
	border-radius:2px;
}
 
.selectbox li:hover {
	color: #FFF;
}
