.selectbox-wrapper > .selectbox > .dropdown {
    position: absolute;
    z-index: 9999;
    overflow-y: auto;
    overflow-x: hidden;
    list-style: none;
    display: block;
    height: auto;
}
.selectbox-wrapper > .selectbox > .select  {
    float:left;
    position:relative;
    z-index:10000
}
.selectbox-wrapper > .selectbox {
    display: inline-block;
    position: relative;
    z-index: 2;
}


